import React, { useState } from "react";
import { ButtonWrap, Wrap, } from "./styled";
import { Table, Input, Popconfirm, Button as AntButton } from 'antd';
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { modifyServiceAction, useServices, } from "./action";
import { deleteService, IServices } from "../../services/services";



export default () => {

  const [count, setCount] = useState(0);
  const [current, setCurrent] = useState(1);

  const { services, loading, setLoading, setServices } = useServices(count);


  const handleChangeValue = (key: keyof IServices, value: any, item: IServices) => {
    const newData = services.map(v => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setServices(newData);
  }

  const columns = [
    {
      title: '客服ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '客服姓名',
      dataIndex: 'customer_service_name',
      key: 'customer_service_name',
      render: (value: string, item: IServices) => {
        if (item.isModify) {
          return <Input
            allowClear
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('customer_service_name', e.target.value, item) }}
          />
        } else {
          return value ? value : '-';
        }
      }
    },
    {
      title: '客服联系信息',
      dataIndex: 'customer_service_info',
      key: 'customer_service_info',
      render: (value: string, item: IServices) => {
        if (item.isModify) {
          return <Input
            allowClear
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('customer_service_info', e.target.value, item) }}
          />
        } else {
          return value ? value : '-';
        }
      }
    },
    {
      title: '操作',
      key: 'operate',
      render: (item: IServices) => {
        if (item.isModify) {
          return <>
            <Button
              text='确定'
              style={{ marginRight: 10 }}
              onPress={async () => {
                const res = await modifyServiceAction(item);
                if (res) {
                  setCount(v => v + 1);
                }
              }}
            />
            <Button
              text='取消'
              onPress={async () => {
                setCount(v => v + 1);
              }}
            />
          </>
        }
        return <>
          <Button
            text='修改'
            style={{ marginRight: 10 }}
            onPress={async () => {
              const newData = services.map(v => v.id === item.id ? { ...v, isModify: true } : v);
              setServices(newData);
            }}
          />
          <Popconfirm
            title="确定删除此客服？"
            placement="topLeft"
            transitionName=''
            onConfirm={async () => {
              setLoading(true);
              const res = await deleteService(item.id);
              setLoading(false);
              if (res.executed) {
                setCount(v => v + 1);
              }
            }}
          >
            <AntButton>删除</AntButton>
          </Popconfirm>

        </>
      }
    },
  ];


  return (
    <Wrap>

      <ButtonWrap>
        <Button onPress={async () => {
           setCurrent(1);
          setServices(v => [{
            id: 0,
            customer_service_name: "",
            customer_service_info: '',
            isModify: true
          }, ...v,])
        }}
          text='新增客服'
        />
      </ButtonWrap>

      <Table
        pagination={{
          hideOnSinglePage: true,
          current: current,
          onChange: (page) => {
            setCurrent(page);
          }
        }}
        rowKey={(item) => item.id}
        dataSource={services}
        columns={columns.map(v => ({ ...v, align: 'center' }))}
      />
      <LoadingPage isLoading={loading} />
    </Wrap>
  )
};
