import { message } from "antd";
import { useEffect, useState } from "react"
import { fetchServices, IServices, modifyService } from "../../services/services";


export const useServices = (count: number) => {
    const [services, setServices] = useState<IServices[]>([]);
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        setLoading(true);
        fetchServices().then((v) => {
            const newData = v.data_list;
            newData.reverse();
            setServices(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, [count]);

    return {
        loading,
        services,
        setServices,
        setLoading
    }
}

export const modifyServiceAction = async (item: IServices) => {
    const res = await modifyService(item);
    return res.executed;
}
